<template>
  <div class="white--text">
    <v-overlay :value="isLoading" style="z-index:999">
      <v-progress-circular
        indeterminate
        size="64"
      ></v-progress-circular>
    </v-overlay>
    <template>
      <v-card style="position: relative;" elevation="3">
        <!-- / -->
        <v-dialog max-width="500" v-model="dialog" persistent>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              class="theme-btn ml-4"
              dark
              v-bind="attrs"
              v-on="on"
              absolute
              bottom
            ></v-btn>
          </template>
          <v-card>
            <v-card-title>{{this.form._id ? '': ''}}</v-card-title>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <file-pond
                      ref="pond"
                      name="cover"
                      label-idle=""
                      allow-multiple="false"
                      accepted-file-types="image/jpeg, image/png"
                      imageResizeTargetWidth="100"
                      @updatefiles="getCoverFiles($event)"
                    />
                  </v-col>
                  <v-col cols="12">
                    <v-text-field label="" v-model="form.title"></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-select
                      v-model="form.type"
                      :items="items"
                      item-text="type"
                      placeholder=""
                    ></v-select>
                  </v-col>
                  <v-col cols="12">
                    <v-textarea label="" v-model="form.description" class="text-wrapper"></v-textarea>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn @click="resetForm"></v-btn>
              <v-btn
                color="success"
                @click="submitAdminPlaylist"
                :disabled="modalSubmitting"
              >{{ this.form._id ? '':''}}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-card-title>
          
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <!--  -->
        <v-data-table :headers="headers" :items="playlists" :search="search">
          <template v-slot:item.edit="{ item }">
            <v-icon small class="mr-2" @click="openEditForm(item)">mdi-pencil</v-icon>
          </template>
          <template v-slot:item.del="{ item }">
            <v-icon small class="mr-2" @click="deletePlaylist(item)">mdi-trash-can</v-icon>
          </template>
          <template v-slot:item.cover="{ item }">
            <div class="p-2">
              <v-img :src="item.cover" width="100px"></v-img>
            </div>
          </template>
        </v-data-table>
      </v-card>
    </template>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isLoading: false,
      dialog: false,
      search: '',
      headers: [
        {
          text: '',
          align: 'start',
          sortable: false,
          value: 'title'
        },
        { text: '', value: 'cover', sortable: false },
        { text: '', value: 'type' },
        { text: '', value: 'createDate' },
        { text: '', value: 'description' },
        { text: '', value: 'edit', sortable: false },
        { text: '', value: 'del', sortable: false }
      ],
      items: [
        { type: 'festival' },
        { type: 'vibe' }
      ],
      playlists: [],
      modalSubmitting: false,
      // 
      form: {
        title: '',
        type: '',
        cover: null,
        description: '',
        _id: '',
        role: 1
      },
      state: true
    }
  },
  methods: {
    getCoverFiles (event) {
      if (event[0]) {
        this.form.cover = event[0].file
      }
    },
    // adminplaylists
    async getAdminPlaylists () {
      try {
        const { data } = await this.api.get('/playlists?owner=' + this.user._id)
        this.playlists = data.result
        for (let i = 0; i < this.playlists.length; i++) {
          this.playlists[i].createDate = new Date(this.playlists[i].createDate).toLocaleDateString().replace(/\//g, '.')
        }
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: '',
          text: ''
        })
      }
    },
    // 
    openEditForm (item) {
      this.dialog = true
      // iddata
      this.form._id = item._id
      this.form.title = item.title
      this.form.type = item.type
      this.form.description = item.description
      this.form.cover = null
    },
    // reset
    resetForm () {
      this.dialog = false
      this.form = {
        title: '',
        type: '',
        cover: null,
        description: null,
        _id: '',
        role: 1
      }
    },
    // / admin
    async submitAdminPlaylist () {
      // 
      this.modalSubmitting = true
      this.isLoading = true
      // formdata
      const fd = new FormData()
      for (const key in this.form) {
        if (key !== '_id') {
        // formdata
          fd.append(key, this.form[key])
        }
      }
      try {
        // 
        if (this.form._id.length === 0) {
          await this.api.post('/playlists/admin', fd, {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          })
          this.$swal({
            icon: 'success',
            title: '',
            text: '',
            confirmButtonColor: '#4DB6AC'
          })
        } else {
          // 
          await this.api.patch('/playlists/admin/' + this.form._id, fd, {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          })
          this.$swal({
            icon: 'success',
            title: '',
            text: '',
            confirmButtonColor: '#4DB6AC'
          })
        }
        this.getAdminPlaylists()
        this.resetForm()
        this.isLoading = false
        this.$refs.pond.removeFile()
      } catch (error) {
        this.$swal({
          icon: 'error',
          title: '',
          text: error.response.data.message,
          confirmButtonColor: '#4DB6AC'
        })
      }
      this.modalSubmitting = false
      this.isLoading = false
    },
    // 
    async deletePlaylist (item) {
      this.$swal({
        icon: 'warning',
        title: '',
        text: '',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then((result) => {
        if (result.isConfirmed) {
          this.api.delete('/playlists/' + item._id, {
            headers: {
              authorization: 'Bearer ' + this.user.token
            }
          }).then(() => {
            this.getAdminPlaylists()
            this.$swal({
              icon: 'success',
              title: '',
              text: ''
            })
          }).catch((error) => {
            this.$swal({
              icon: 'error',
              title: '',
              text: error.response.data.message
            })
          })
        } else {
          this.$swal.close()
        }
      })
    }
  },
  async created () {
    this.getAdminPlaylists()
  }
}
</script>
